*,
*:before,
*:after {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
  font-size: 1.3rem;
  color: #000000;
}
a {
  color: red;
  text-decoration: none;
}
a:hover,
a:focus {
  text-decoration: underline;
}
